import { solid } from "@fortawesome/fontawesome-svg-core/import.macro"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { IconButton, List, ListItem, ListItemButton, ListItemText, Typography } from "@mui/material"
import { Box } from "@mui/system"
import { Link } from "react-router-dom"
import { documentTypes } from "../../configs/constant"

const TokenDocuments = props => {
  const { documents } = props
  
  const downloadItem = (url) => {
    const link = document.createElement('a');
    link.setAttribute('rel', 'noreferrer');
    link.href = url;
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  return (
    <Box className="gallery">
      <Typography className="title" gutterBottom variant="h5" component="div">
        <FontAwesomeIcon icon={solid('folder-open')} />Documents</Typography>
      <Typography variant="body2" color="text.secondary">
        {documentTypes.map(type => {
          let files = documents.filter(item => item.path === type.path );
          if ( files.length <= 0 ) return <></>;
          return (
            <Box sx={{ border: '1px solid var(--unikbase-gray)', borderRadius: '4px', p: '0.5em!important', mb: 2 }}>
              {files.length > 1 ? (
                <>
                  <Typography variant="h6">{type.name}</Typography>
                  <List>
                    {files.map(file => (
                      <ListItem
                        key={file.uuid}
                        secondaryAction={
                          <IconButton onClick={() => downloadItem(file.fileUrl)} edge="end" aria-label="delete">
                            <FontAwesomeIcon icon={solid('cloud-arrow-down')} />
                          </IconButton>
                        }
                      >
                        <ListItemText primary={file.name} />
                      </ListItem>
                    ))}
                  </List>
                </>
              ) : (
                <List sx={{ p: 0 }}>
                    {files.map(file => (
                      <ListItem
                        key={file.uuid}
                        secondaryAction={
                          <IconButton onClick={() => downloadItem(file.fileUrl)} edge="end" aria-label="delete">
                            <FontAwesomeIcon icon={solid('cloud-arrow-down')} />
                          </IconButton>
                        }
                      >
                        <ListItemText primary={type.name} />
                      </ListItem>
                    ))}
                  </List>
              )}
            </Box>
          )
        })}
      </Typography>
    </Box>
  )
}

export default TokenDocuments;